        title: 'Bubble & Squeak',
        category: 'Pork',
        area: 'British',
        instructions:
            'Melt the fat in a non-stick pan, allow it to get nice and hot, then add the bacon. As it begins to brown, add the onion and garlic. Next, add the sliced sprouts or cabbage and let it colour slightly. All this will take 5-6 mins.\r\nNext, add the potato. Work everything together in the pan and push it down so that the mixture covers the base of the pan  allow the mixture to catch slightly on the base of the pan before turning it over and doing the same again. Its the bits of potato that catch in the pan that define the term bubble and squeak, so be brave and let the mixture colour. Cut into wedges and serve.',
