        title: 'Walnut Roll Guvara',
        category: 'Dessert',
        area: 'Croatian',
        instructions:
            'Mix all the ingredients for the dough together and knead well. Cover the dough and put to rise until doubled in size which should take about 2 hours. Knock back the dough and knead lightly.\r\n\r\nDivide the dough into two equal pieces; roll each piece into an oblong about 12 inches by 8 inches. Mix the filling ingredients together and divide between the dough, spreading over each piece. Roll up the oblongs as tightly as possible to give two 12 inch sausages. Place these side by side, touching each other, on a greased baking sheet. Cover and leave to rise for about 40 minutes. Heat oven to 200C (425F). Bake for 30-35 minutes until well risen and golden brown. Bread should sound hollow when the base is tapped.\r\n\r\nRemove from oven and brush the hot bread top with milk. Sift with a generous covering of icing sugar.',
        description:
            "Walnut Roll Guvara is a sweet and nutty dessert made with a tender yeast dough, ground walnuts, sugar, and spices. It's a traditional pastry in Croatia and the Balkans, often served on holidays and special occasions.",
