        category: 'Side',
        area: 'Turkish',
        instructions:
            'If you order kumpir in Turkey, the standard filling is first, lots of butter mashed into the potato, followed by cheese. Theres then a row of other toppings that you can just point at to your hearts content  sweetcorn, olives, salami, coleslaw, Russian salad, allsorts  and you walk away with an over-stuffed potato because you got ever-excited by the choices on offer.\r\n\r\nGrate (roughly  you can use as much as you like) 150g of cheese.\r\nFinely chop one onion and one sweet red pepper.\r\nPut these ingredients into a large bowl with a good sprinkling of salt and pepper, chilli flakes (optional).',
